import React from 'react'
import ProjectTypeIcons from '../projectTypes/projectTypes'
import CertifiedIcon from '../misc/certified'

const ProjectTypeContainer = (props) => {
    const {type, certified = false} = props;
    return (
       
                <>
                    <div className="icons">
                        <ProjectTypeIcons type={type} />
                        <div className="certified"><CertifiedIcon /></div>
                    </div>
                        
                    <style jsx>
                {`  

                    .icons {
                        width: 40px;
                        height: 40px;

                        background: #4E7876;
                        border: 1px solid #225755;
                        border-radius: 3px;

                        display: flex;
                        align-items:center;
                        justify-content: center;

                    }

                    .certified {
                        ${true ? 'display: none;' : ''}
                        width: 18px;
                        position: absolute;
                        top: -7px;
                        right: -11px;
                    }



                   
                  
                `}

                </style>

                </>
              

    )
}

export default ProjectTypeContainer;